<template>
  <app-layout>
    <div class="inner-contents">
      <h2 class="contents-title"></h2>
      <p class="contents-text" v-if="!show">( <span class="text-danger">*</span>   .)</p>
      <div v-if="show">
        <p class="contents-text">   .</p>
        <p class="contents-text mt-2">(  <router-link :to="'/signin'" class="text-primary"></router-link> !)</p>
        <div class="select-container">
          <router-link :to="'/signup/student'" class="select-link">
            <i class="fas fa-user icon"></i>
            <h3 class="title"> </h3>
            <p class="text">    !</p>
          </router-link>
          <router-link :to="'/signup/instructor'" class="select-link">
            <i class="fas fa-microphone icon"></i>
            <h3 class="title"> </h3>
            <p class="text">   !</p>
          </router-link>
        </div>
      </div>
      <router-view/>
    </div>
  </app-layout>
</template>

<script>
import AppLayout from '@/components/layout/AppLayout'
export default {
  name: 'SignUp',
  data () {
    return {
      show: true
    }
  },
  methods: {
    onShow () {
      if (this.$route.path !== '/signup') {
        this.show = false
      } else {
        this.show = true
      }
    }
  },
  created () {
    this.onShow()
  },
  watch: {
    '$route': 'onShow'
  },
  components: {
    AppLayout
  }
}
</script>



